<template>
  <div class="page">
    <head-top head-title="" :go-back='true'></head-top>
    <section id="scroll_section" class="scroll_container">
      <div class="markdown">
        <h3 id="q1-">Q1: </h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <h3 id="q2-">Q2: </h3>
        <p></p>
        <h3 id="q3-">Q3: </h3>
        <p>7.33030</p>
        <h3 id="q4-">Q4: </h3>
        <p></p>
        <h3 id="q5-">Q5: </h3>
        <p></p>
        <h3 id="q6-">Q6: </h3>
        <p> &gt;  &gt; </p>
        <h3 id="q7-">Q7: </h3>
        <p></p>
        <h3 id="q8-">Q8: </h3>
        <ul>
          <li></li>
          <li>APP</li>
        </ul>
        <h3 id="q9-">Q9: </h3>
        <ul>
          <li>XXXXX</li>
        </ul>
        <h3 id="q10-">Q10:  ?</h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li>12</li>
          <li></li>
          <li></li>
          <li>10105757</li>
        </ul>
      </div>
    </section>
  </div>
</template>

<script>
  import headTop from 'src/components/header/head'
  import BScroll from 'better-scroll'

  export default {
    mounted(){
      this.$nextTick(() => {
        new BScroll('#scroll_section', {
          deceleration: 0.001,
          bounce: true,
          swipeTime: 1800,
          click: true,
        });
      })
    },
    components: {
      headTop
    },
  }
</script>

<style lang="scss" scoped>
  @import '../../../../src/style/mixin';

  .page{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 1.95rem;
    z-index: 203;
    background-color: #fff;
    p, span{
      font-family: Helvetica Neue,Tahoma,Arial;
    }
  }
  .scroll_container{
    @include wh(100%, 100%);
  }
  .markdown{
    padding-bottom: 2rem;
    @include sc(.6rem, #666);
    padding: 0 0.5rem 0.5rem;
    h3{
      color:$colorText;
      line-height: 2rem;
    }
    p,li{
      @include sc(.6rem, #666);
      line-height: 1rem;
    }
  }
</style>



